<template>
  <v-app>
    <v-container grid-list-md text-xs-center>
      <v-layout>
        <v-flex xs12>
          <v-card>
            <v-card-title primary-title>
              <div>
                <h3 class="headline mb-0">Cadastro Pessoal</h3>
              </div>
            </v-card-title>
            <v-form v-model="valid">
              <v-container >
                <v-layout>
                  <v-flex xs12>
                    <v-text-field
                      v-model="form.nome"
                      :rules="nameRules"
                      :counter="50"
                      label="Nome..."
                      required
                    ></v-text-field>
                  </v-flex>
                </v-layout>
                <v-layout>
                  <v-flex xs12>
                    <v-text-field
                      v-model="form.cpf"
                      :rules="numbersOnly"
                      :counter="11"
                      label="Cpf.."
                      type="number"
                      required
                    ></v-text-field>
                  </v-flex>
                </v-layout>
                <v-radio-group v-model="dbSelecionado">
                  <v-radio v-for="n in dbs" :key="n" :label="`${n}`" :value="n"></v-radio>
                </v-radio-group>
              </v-container>
            </v-form>
            <v-card-actions>
              <v-btn text color="primary" @click="cadastrarNovo">Cadastrar</v-btn>
            </v-card-actions>
          </v-card>
          <br />
          <v-card v-if="respostaBemSucedida">
            <v-alert :value="true" type="success">Nome: {{formRes.nome}}</v-alert>
            <v-alert :value="true" type="success">CPF: {{formRes.cpf}}</v-alert>
          </v-card>
          <v-card v-if="cpfNaoEncontrado">
            <v-alert :value="true" type="error">CPF não encontrado!</v-alert>
          </v-card>
          <br />
          <!-- <br>  Separador -->
          <v-card>
            <v-card-title primary-title>
              <div>
                <h3 class="headline mb-0">Autenticação Pessoal</h3>
              </div>
            </v-card-title>
            <v-form v-model="valid">
              <v-container>
                <v-layout>
                  <v-flex xs12>
                    <v-text-field
                      v-model="cpfPesquisa"
                      :rules="numbersOnly"
                      :counter="11"
                      label="Cpf"
                      type="number"
                      required
                    ></v-text-field>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-form>
            <v-card-actions>
              <v-btn text color="primary" @click="pesquisarPessoa(cpfPesquisa)">Pesquisar</v-btn>
            </v-card-actions>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </v-app>
</template>

<script>
export default {
  name: "App",
  components: {},
  data() {
    return {
      form: {
        nome: "",
        cpf: ""
      },
      cpfPesquisa: "",
      formRes: {
        nome: "",
        cpf: ""
      },
      dbs: ["Db1", "Db2"],
      dbSelecionado: "",
      nameRules: [
        v => !!v || "Name is required",
        v => v.length <= 50 || "Name must be less than 50 characters"
      ],
      numbersOnly: [
        v => !!v || "CPF is required",
        v => v >= 11 || "CPF must be 11 characters"
      ],
      respostaBemSucedida: "",
      cpfNaoEncontrado: "",
      cpfJaCadastrado: "",
      valid: true
    };
  },
  methods: {
    async cadastrarNovo() {
      let dbselected = "";

      if (this.dbSelecionado == "Db1") {
        dbselected = "https://db-test-f878e.firebaseio.com/";
      } else if (this.dbSelecionado == "Db2") {
        dbselected = "https://dbdois-test.firebaseio.com/";
      } else {
        alert("Selecione o DataBase!");
      }
      if (this.form.nome && this.form.cpf.toString().length == 11) {
        if (await this.verificarCpf(this.form.cpf)) {
          await this.$http.post(dbselected + "pessoas.json", this.form).then(res => alert("Pessoa cadastrada"));
        } else {
          alert("Cpf já cadastrado");
        }
      }
      this.limparCampos();
    },

    async pesquisarPessoa(cpf) {
      let todoMundo = [];

      await this.$http
        .get("https://db-test-f878e.firebaseio.com/pessoas.json")
        .then(res => {
          for (let x in res.data) {
            todoMundo.push(res.data[x]);
          }
        });
      await this.$http
        .get("https://dbdois-test.firebaseio.com/pessoas.json")
        .then(res => {
          for (let x in res.data) {
            todoMundo.push(res.data[x]);
          }
        });

      
      await this.verificarPesquisa(todoMundo, cpf);
    },

    verificarPesquisa(arrayDeObjs, cpf) {

      if(this.respostaBemSucedidaMethod(arrayDeObjs, cpf)){
        this.cpfNaoEncontrado= true
        setTimeout(() => {
            this.cpfNaoEncontrado = false;
            this.limparCampos();
          }, 3000);
      }
      

      
    },

    respostaBemSucedidaMethod(arrayDeObjs, cpf){
      let resultadoRes = true
          arrayDeObjs.forEach(x => {
        if (x.cpf == cpf) {
          this.respostaBemSucedida = true;
          this.formRes.nome = x.nome;
          this.formRes.cpf = x.cpf;
          resultadoRes = false
          setTimeout(() => {
            this.respostaBemSucedida = false;
            this.limparCampos();
          }, 5000);
        }
      });
      return resultadoRes
    },

    async verificarCpf(cpf_item) {
      let cpfs = [];
      await this.$http
        .get("https://db-test-f878e.firebaseio.com/pessoas.json")
        .then(res => {
          for (let x in res.data) {
            cpfs.push(res.data[x].cpf);
          }
        });
      await this.$http
        .get("https://dbdois-test.firebaseio.com/pessoas.json")
        .then(res => {
          for (let x in res.data) {
            cpfs.push(res.data[x].cpf);
          }
        });

      let resultado = await this.comparaCpfs(cpfs, cpf_item);
      return resultado;
    },

    comparaCpfs(cpf_array, cpf_item) {
      
      let resultado = true;
      cpf_array.forEach(x => {
        if (x == cpf_item) {
          resultado = false;
        }
      });
      return resultado;
    },

    limparCampos() {
      this.form.nome = "";
      this.form.cpf = "";
      this.cpfPesquisa = ''
      this.formRes.nome = "";
      this.formRes.cpf = "";
    }
  }
};
</script>
